.overlay {
  height: 100%;
  min-height: 100%;
  padding-bottom: 65px;
  background: #2d2d2d;
  filter: alpha(opacity=90);
  background: rgba(45, 45, 45, 0.65);
  position: absolute;
  top: 0;
  width: 100%; }

/* Header */
.large-header {
  position: relative;
  overflow: hidden;
  background-size: cover;
  z-index: 1;
  font-weight: 400;
  font-size: 1em;
  line-height: 1.25;
  font-family: "Raleway", Calibri, Arial, sans-serif;
  /*background: #333 url('../images/demo-1-bg.jpg') center 0 no-repeat fixed;*/
  background: #333 url(../../assets/images/jGIGWOA.jpg) center 0 repeat fixed;
  height: 100vh; }

.main-title {
  padding: 10px;
  color: #f9f1e9;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  height: 100vh;
  padding-top: 50vh; }

.typewriter {
  overflow: hidden;
  /* Ensures the content is not revealed until the animation */
  border-right: 0.15em solid orange;
  /* The typwriter cursor */
  white-space: nowrap;
  /* Keeps the content on a single line */
  margin: 0 auto;
  /* Gives that scrolling effect as the typing happens */
  letter-spacing: 0.15em;
  /* Adjust as needed */
  animation: typing 3s steps(60, end), blink-caret 0.75s step-end infinite; }

/* The typing effect */
@keyframes typing {
  from {
    width: 0; }
  to {
    width: 100%; } }

/* The typewriter cursor effect */
@keyframes blink-caret {
  from,
  to {
    border-color: transparent; }
  50% {
    border-color: orange; } }
